import { Injectable } from '@angular/core';
import { SendInformationService } from '../SendInformation/send-information.service';
import { ACTIONFROMSTEP, ACTION_FROM_RETOMA } from 'src/mbaas/mbaas.const';
import { Modal } from '../../components/modal/modal';
import { AlertModalTMAComponent } from '../../components/modal/alert-modal-tma/alert-modal-tma.component';
import { PostMessageService } from '../postMessage/post-message.service';
import { WorkflowService } from '../workflow/workflow.service';
import { MBAAS_STEPS } from 'src/mbaas/mbaas.route.const';

@Injectable({
  providedIn: 'root'
})
export class ModalBuilderService {

  constructor(
    private obser: SendInformationService<any>,
    private postMessage: PostMessageService
  ) { }

  getModalData( response: any, status: any, data: any = {} ): Modal {
    return {
      // tslint:disable-next-line: no-shadowed-variable
      2: (response) => this.redirecTo(response),
      // tslint:disable-next-line: no-shadowed-variable
      3: (response) => this.simple(response),
      // tslint:disable-next-line: no-shadowed-variable
      4: (response) => this.actionFromStep(response),
      // tslint:disable-next-line: no-shadowed-variable
      5: (response, data) => this.actionFromRetoma(response, data)
    }[status](response, data);
  }

  redirecTo(response: any): Modal {
    return {
      display: true,
      buttons: response.buttons.map( item => {
        // item.class = 'alertModal__footer--button alertModal__footer--button--primary';
        item.class = item.class || 'button button--primary';
        const url = item.callback.toString();
        item.callback = ( close ) => {
          // window.open(url || '', '_blank');
          const data = {
            status: 'ok',
            statusCode: 200,
            statusMessage: url
          };
          this.postMessage.postMessage('appFinish', data);
          close();
        };
        return item;
      }),
      title: response.title,
      message: response.message,
      entryComponent: AlertModalTMAComponent
    };
  }

  simple(response: any): Modal {
    return {
      display: true,
      buttons: response.buttons.map( item => {
        // item.class = 'alertModal__footer--button alertModal__footer--button--primary';
        item.class = item.class || 'button button--primary';
        item.callback = ( close ) => close();
        return item;
      }),
      title: response.title,
      message: response.message,
      entryComponent: AlertModalTMAComponent
    };
  }

  actionFromStep(response: any): Modal {
    return {
      display: true,
      buttons: response.buttons.map( item => {
        // item.class = 'alertModal__footer--button alertModal__footer--button--primary';
        item.class = item.class || 'button button--primary';
        item.callback = ( close ) => this.obser.lastValue(ACTIONFROMSTEP)(close);
        return item;
      }),
      title: response.title,
      message: response.message,
      entryComponent: AlertModalTMAComponent
    };
  }

  actionFromRetoma(response: any, data: any): Modal {
    return {
      display: true,
      buttons: response.buttons.map( item => {
        item.class = item.class || 'button button--primary';
        item.callback = ( close ) => this.obser.lastValue(ACTION_FROM_RETOMA)(close, data);
        return item;
      }),
      title: response.title,
      message: response.message,
      entryComponent: AlertModalTMAComponent
    };
  }
}
