import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { JwtModule } from '@auth0/angular-jwt';
import { HttpClient, HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { DeviceDetectorModule } from 'ngx-device-detector';

import { MbaasRoutingModule } from './mbaas-routing.module';
import { MbaasComponent } from './mbaas.component';
import { SendInformationService } from './core/service/SendInformation/send-information.service';
import { LoggerModule, LoggerComponent } from './logger/logger.index';
import { ACCESS_TOKEN, TRANSLATE } from './mbaas.const';
import { CriptoService } from './core/service/encryption/cripto.service';
import { AlertModalTMAComponent } from './core/components/modal/alert-modal-tma/alert-modal-tma.component';
import { ModalComponent } from './core/components/modal/modal.component';
import { ModalModule } from './core/components/modal/modal.module';
import { ValidationRulesService } from './core/service/validationRules/validation-rules.service';
import { WorkflowService } from './core/service/workflow/workflow.service';
import { ModalBuilderService } from './core/service/ModalBuilder/modal-builder.service';
import { InterceptorHttpService } from './core/service/interceptorHttp/interceptor-http.service';
import { AuthService } from './core/service/authentication/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { InfoModalTMAComponent } from './core/components/modal/info-modal-tma/info-modal-tma.component';

@NgModule({
  entryComponents: [
    AlertModalTMAComponent,
    InfoModalTMAComponent
  ],
  declarations: [
    MbaasComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    MbaasRoutingModule,
    LoggerModule,
    ModalModule,
    HttpClientModule,
    DeviceDetectorModule.forRoot(),
    JwtModule.forRoot({
      config: {
        tokenGetter: jwtTokenGetter,
        whitelistedDomains: ['.*']
      }
    }),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: initTraslate,
        deps: [HttpClient]
      }
    }),
  ],
  providers: [
    SendInformationService,
    ValidationRulesService,
    WorkflowService,
    ModalBuilderService,
    CriptoService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: InterceptorHttpService,
      deps: [AuthService, Router],
      multi: true
    }
  ],
  bootstrap: [
    MbaasComponent,
    LoggerComponent,
    ModalComponent
  ]
})
export class MbaasModule { }

export function jwtTokenGetter() {
  return localStorage.getItem(ACCESS_TOKEN);
}
export function initTraslate(http: HttpClient) {
  return new TranslateHttpLoader(http, TRANSLATE.URL, '.json');
}
