import { Injectable } from '@angular/core';
import { Validators, FormGroup, ValidatorFn, ValidationErrors, FormArray } from '@angular/forms';
import * as date from 'date-and-time';
import * as moment from 'moment';
import { SendInformationService } from '../SendInformation/send-information.service';

@Injectable({
  providedIn: 'root'
})
export class ValidationRulesService {

  constructor(public obser: SendInformationService<any>) {}

  /*
    * Validations
  */
  validationForEmail(): Validators {
    // tslint:disable-next-line: max-line-length
    return Validators.pattern(/^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/);
  }

  validationForEmailHN(): Validators {
    // tslint:disable-next-line: max-line-length
    return Validators.pattern(/^(([^<>()[\]\.;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/);
  }

  validationForNullValue(): Validators {
    return Validators.nullValidator;
  }

  validationForOnlyNumbers(): Validators {
    return Validators.pattern('^([1234567890])*$');
  }

  validationForRequired(done: (value: FormGroup) => boolean = (value: FormGroup) => false ): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[this.errorTypeRequired] = true;
      if ( !control.parent) {
        return obj;
      }
      if ( done(control) ) {
        return null;
      }
      if ( typeof control.value === 'string') {
        if ( control.value === '' ) {
          return obj;
        }
      }
      if ( typeof control.value === 'number') {
        // tslint:disable-next-line: use-isnan
        if ( control.value === NaN ) {
          return obj;
        }
      }
      if ( typeof control.value === 'boolean') {
        if ( control.value === false ) {
          return obj;
        }
      }
      return null;
    };
  }

  validationForRequiredTrue(): Validators {
    return Validators.requiredTrue;
  }

  validationForMinRequired(controlName: string, minRequired = 1): Validators | ValidatorFn {
    return (formGroup: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[this.errorTypeMinRequired] = minRequired;
      if ( !formGroup.parent ) {
        return obj;
      }
      let checked = 0;
      Object.keys(formGroup.controls).forEach(key => {
        const control = formGroup.controls[key];
        if (control.get(controlName).value === true) {
          checked++;
        }
      });
      if (checked < minRequired) {
        return obj;
      }
      return null;
    };
  }

  validationForMaxNumber( n: number, done: (value: FormGroup) => boolean = (value: FormGroup) => false ): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[this.errorTypeMaxNumber] = n;
      if ( !control.parent) {
        return obj;
      }
      if ( done(control) ) {
        return null;
      }
      if ( Number(control.value) > Number(n) ) {
        return obj;
      }
      return null;
    };
  }

  validationForMinNumber( n: number, done: (value: FormGroup) => boolean = (value: FormGroup) => false ): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[this.errorTypeMinNumber] = n;
      if ( !control.parent) {
        return obj;
      }
      if ( done(control) ) {
        return null;
      }
      if ( Number(control.value) < Number(n) ) {
        return obj;
      }
      return null;
    };
  }

  validationWithCallBack( key: string, done: (value: FormGroup) => boolean): Validators {
      return (control: FormGroup): ValidationErrors | null => {
        const obj = {};
        obj[key] = true;
        if ( !control.parent) {
          return obj;
        }
        return done(control) ? null : obj ;
      };
  }

  phoneNumber3FieldsValidation( key: string): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[key] = true;
      if (  control.get('actividadLaboral').value === 'A' ||
            control.get('actividadLaboral').value === 'P' ||
            control.get('actividadLaboral').value === 'T' ) {
        return null;
      }
      if ( control.get('indicativo').value === '' && control.get('telefono').value === '' && control.get('extencion').value === '' ) {
        return null;
      }
      if ( control.get('indicativo').value !== '' && control.get('telefono').value !== '') {
        return null;
      }
      return obj ;
    };
}

  validationWithArrayCallBack( key: string, done: Array<(value: FormGroup) => boolean>): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[key] = true;
      if ( !control.parent) {
        return obj;
      }
      const results = done.map(
        item => item(control)
      );
      return results.reduce((itemA, itemB) => itemA && itemB, true) ? null : obj ;
    };
}

  validationForMinLength( n: number, done: (value: FormGroup) => boolean = (value: FormGroup) => false ): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[this.errorTypeMinLength] = n;
      if ( !control.parent) {
        return obj;
      }
      if ( done(control) ) {
        return null;
      }
      if ( (control.value as string).length === 0 ) {
        return null;
      }
      if ( n > (control.value as string).length ) {
        return obj;
      }
      return null;
    };
  }

  validationForMaxLength( n: number, done: (value: FormGroup) => boolean = (value: FormGroup) => false ): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[this.errorTypeMaxLength] = n;
      if ( !control.parent) {
        return obj;
      }
      if ( done(control) ) {
        return null;
      }
      if ( n < (control.value as string).length ) {
        return obj;
      }
      return null;
    };
  }

  validationForNotAllow(text: string, done: (value: FormGroup) => boolean = (value: FormGroup) => false ): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[this.errorTypeNotAllow] = true;
      if ( !control.parent) {
        return obj;
      }
      if ( done(control) ) {
        return null;
      }
      if ( text === control.value ) {
        return obj;
      }
      return null;
    };
  }

  validationForPattern(pattern: string | RegExp): Validators {
    return Validators.pattern(pattern);
  }

  validationForRegExp( pattern: RegExp, done: (value: FormGroup) => boolean = (value: FormGroup) => false ): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[this.errorTypeRegExp] = true;
      if ( !control.parent) {
        return obj;
      }
      if ( done(control) ) {
        return null;
      }
      if ( !pattern.test(control.value) ) {
        return obj;
      }
      return null;
    };
  }

  validationClaveVirtual(field1: string, field3: string): ValidatorFn {
    const obj = {};
    obj[field3] = true;
    return (control: FormGroup): ValidationErrors | null => {
      const str = control.get(field1).value;
      const n = str.search(
      /(0123|1234|2345|3456|4567|5678|6789|7890|3210|4321|5432|6543|7654|8765|9876|0987|0000|1111|2222|3333|4444|5555|6666|7777|8888|9999)/i
        );
      return (n === -1)  ? null : obj;
    };
  }

  validationForValue(field1: string, value1: string, field2: string, field3: string): ValidatorFn {
    const obj = {};
    obj[field3] = true;
    return (control: FormGroup): ValidationErrors | null => {
      const fieldUno = control.get(field1).value;
      if ( fieldUno === value1 ) {
        const fieldDos = control.get(field2).value;
        if ( fieldDos === '') {
          return obj;
        }
        return null;
      }
      return null;
    };
  }

  validationForSelect(valores: () => Array<any>, required: (control: FormGroup) => boolean = (control: FormGroup) => true): Validators {
    return (control: FormGroup): ValidationErrors | null => {
        if ( required(control) )  {
          return null;
        }
        return valores().filter( item => {
          if ( item.value === control.value) {
            return true;
          }
          return false;
        }).length >= 1 ? null : { select: true };
    };
  }

  validationForSelectMultiple(
    valores: () => Array<any>,
    required: (control: FormGroup) => boolean = (control: FormGroup) => true
  ): Validators {
    return (control: FormGroup): ValidationErrors | null => {
        if ( required(control) )  {
          return null;
        }
        const va = valores().filter( item => {
          if ( (control.value as Array<string>).filter( data => item.value === data ).length !== 0 ) {
            return true;
          }
          return false;
        });
        return va.length !== 0 ? null : { select: true };
    };
  }

  validationForMinDate(fecha: string|number, type?: 'year' | 'month' | 'day'): Validators {
    return (control: FormGroup): ValidationErrors | null => {
        if ( !control.parent) {
          return { minDate: true };
        }
        const myDate = new Date(control.value);
        if ( typeof(fecha) === 'number') {
          if (type === 'year') {
            if ( moment(date.addYears(new Date(), fecha)).isAfter(myDate) ) {
              return { minDate: true };
            }
          }
          if (type === 'month') {
            if ( moment(date.addMonths(new Date(), fecha)).isAfter(myDate) ) {
              return { minDate: true };
            }
          }
          if (type === 'day') {
            if ( moment(date.addDays(new Date(), fecha)).isAfter(myDate) ) {
              return { minDate: true };
            }
          }
        }
        if ( typeof(fecha) === 'string') {
          if ( moment(new Date(fecha)).isAfter(myDate) ) {
            return { minDate: true };
          }
        }
        return null;
      };
  }

  validationForMaxDate(fecha: string|number, type: 'year' | 'month' | 'day' = 'day'): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      if ( !control.parent) {
        return { maxDate: true };
      }
      const myDate = new Date(control.value);
      if ( typeof(fecha) === 'number') {
        if (type === 'year') {
          if ( moment(date.addYears(new Date(), fecha)).isBefore(myDate) ) {
            return { maxDate: true };
          }
        }
        if (type === 'month') {
          if ( moment(date.addMonths(new Date(), fecha)).isBefore(myDate) ) {
            return { maxDate: true };
          }
        }
        if (type === 'day') {
          if ( moment(date.addDays(new Date(), fecha)).isBefore(myDate) ) {
            return { maxDate: true };
          }
        }
      }
      if ( typeof(fecha) === 'string') {
        if ( moment(new Date(fecha)).isBefore(myDate) ) {
          return { maxDate: true };
        }
      }
      return null;
    };
  }

  validationContentInArray(field1: string, map: (pais: string) => boolean, field3: string): ValidatorFn {
    const obj = {};
    obj[field3] = true;
    return (control: FormGroup): ValidationErrors | null => {
      const str = control.get(field1).value;
      if ( map(str) ) {
        return null;
      }
      return obj;
    };
  }

  validationContentInArrayWithValue(field1: string, field2: string, value: string, map: ( pais: string ) => boolean, field3: string):
  ValidatorFn {
    const obj = {};
    obj[field3] = true;
    return (control: FormGroup): ValidationErrors | null => {
      const campo2 = control.get(field2).value;
      if (campo2 !== value) {
        return null;
      }
      const str = control.get(field1).value;
      if ( map(str) ) {
        return null;
      }
      return obj;
    };
  }

  validationForContentInArray(array: Array<any>, done: (value: FormGroup) => boolean = (value: FormGroup) => false ): Validators {
    const obj = {};
    obj[this.errorTypeContentInArray] = true;
    return (control: FormGroup): ValidationErrors | null => {
      if ( !control.parent) {
        return obj;
      }
      if ( done(control) ) {
        return null;
      }
      if ( !array.includes(control.value) ) {
        return obj;
      }
      return null;
    };
  }

  validationForBines(catalogBines: any, done: (value: FormGroup) => boolean = (value: FormGroup) => false ): Validators {
    const bines = catalogBines.map(key => key.bin);
    return (control: FormGroup): ValidationErrors | null => {
      if ( done(control) ) {
        return null;
      }
      const bin = control.value.length > 9 ? control.value.substring(0, 9) : control.value;
      if ( !bines.includes(bin) ) {
        return { bin: true };
      }
      const bankType = catalogBines.find(key => key.bin === bin).tipoBanco;
      if (bankType !== 'N') {
        return { bankType: true };
      }
      const length = Number(catalogBines.find(key => key.bin === bin).longitud);
      if (length > (control.value as string).length) {
        return { minlength: length };
      }
      if (length < (control.value as string).length) {
        return { maxlength: length };
      }
      return null;
    };
  }

  disbaleControl( done: (value: FormGroup) => boolean = (value: FormGroup) => false ): Validators | ValidatorFn {
    return (control: FormGroup): ValidationErrors | null => {
      if (!control.parent) {
        return null;
      }
      if ( done(control) ) {
        control.disable();
        return null;
      }
      return null;
    };
  }

  resetMyCallbackValidation(fields: Array<string>): Validators | ValidatorFn {
    return (control: FormGroup): ValidationErrors | null => {
      if (!control.parent) {
        return null;
      }
      fields.forEach(item => {
        control.parent.get(item).enable();
        control.parent.get(item).setValue(control.parent.get(item).value);
      });
      return null;
    };
  }

  resetMyCallbackValidationArray(campo: string, fields: Array<string>): Validators | ValidatorFn {
    return (control: FormGroup): ValidationErrors | null => {
      if ( !control.parent) {
        return null;
      }
      (control.parent.get(campo) as FormArray).controls.forEach(
        itemArr => {
          fields.map( item => {

            itemArr.get(item).setValue(itemArr.get(item).value);
          });
        }
      );
      return null;
    };
  }

  validationForMinDateGroup(
    fecha: string|number,
    type?: 'year' | 'month' | 'day',
    requerida: (control: FormGroup) => boolean = (control) => true
  ): ValidatorFn {
    return (control: FormGroup): ValidationErrors | null => {
        if ( !requerida(control) ) {
          return null;
        }
        if ( !control.parent) {
          return { minDate: true };
        }
        const myDate = new Date(`${control.get('mes').value}/${control.get('dia').value}/${control.get('anio').value || 'X'}`);
        if ( myDate.toString() === 'Invalid Date') {
          return { invalid: true };
        }
        if (myDate.getDate() !== parseInt(control.get('dia').value, 10)) {
          return { invalid: true };
        }
        if ( typeof(fecha) === 'number') {
          if (type === 'year') {
            if ( moment(date.addYears(new Date(), fecha)).isAfter(myDate) ) {
              return { minDate: true };
            }
          }
          if (type === 'month') {
            if ( moment(date.addMonths(new Date(), fecha)).isAfter(myDate) ) {
              return { minDate: true };
            }
          }
          if (type === 'day') {
            if ( moment(date.addDays(new Date(), fecha)).isAfter(myDate) ) {
              return { minDate: true };
            }
          }
        }
        if ( typeof(fecha) === 'string') {
          if ( moment(new Date(fecha)).isAfter(myDate) ) {
            return { minDate: true };
          }
        }
        return null;
      };
  }

  validationForMaxDateGroup(
    fecha: string | number,
    type: 'year' | 'month' | 'day' = 'day',
    requerida: (control: FormGroup) => boolean = (control) => true
  ): ValidatorFn {
    return (control: FormGroup): ValidationErrors | null => {
      if ( !requerida(control) ) {
        return null;
      }
      if ( !control.parent) {
        return { maxDate: true };
      }
      const myDate = new Date(`${control.get('mes').value}/${control.get('dia').value}/${control.get('anio').value || 'X'}`);
      if ( myDate.toString() === 'Invalid Date') {
        return { invalid: true };
      }
      if (myDate.getDate() !== parseInt(control.get('dia').value, 10)) {
        return { invalid: true };
      }
      if ( typeof(fecha) === 'number') {
        if (type === 'year') {
          if ( moment(date.addYears(new Date(), fecha)).isBefore(myDate) ) {
            return { maxDate: true };
          }
        }
        if (type === 'month') {
          if ( moment(date.addMonths(new Date(), fecha)).isBefore(myDate) ) {
            return { maxDate: true };
          }
        }
        if (type === 'day') {
          if ( moment(date.addDays(new Date(), fecha)).isBefore(myDate) ) {
            return { maxDate: true };
          }
        }
      }
      if ( typeof(fecha) === 'string') {
        if ( moment(new Date(fecha)).isBefore(myDate) ) {
          return { maxDate: true };
        }
      }
      return null;
    };
  }

  /*
    * Tipos de errores
  */
  get errorTypeEmail(): string {
    return 'pattern';
  }

  get errorTypeNullValue(): string {
    return 'null';
  }

  get errorTypeOnlyNumbers(): string {
    return 'pattern';
  }

  get errorTypeRequiredTrue(): string {
    return 'required';
  }

  get errorTypeRequired(): string {
    return 'required';
  }

  get errorTypeMinRequired(): string {
    return 'minRequired';
  }

  get errorTypeMaxNumber(): string {
    return 'max';
  }

  get errorTypeMinNumber(): string {
    return 'min';
  }

  get errorTypeMaxLength(): string {
    return 'maxlength';
  }

  get errorTypeNotAllow(): string {
    return 'notAllow';
  }

  get errorTypeMaxDate(): string {
    return 'maxDate';
  }

  get errorTypeMinDate(): string {
    return 'minDate';
  }

  get errorTypeCelPhone(): string {
    return 'celPhone';
  }

  get errorTypeMinLength(): string {
    return 'minlength';
  }

  get errorTypeInvalidDate(): string {
    return 'invalid';
  }

  get errorTypePattern(): string {
    return 'pattern';
  }

  get errorTypeRegExp(): string {
    return 'pattern';
  }

  get errorTypeSelect(): string {
    return 'select';
  }

  get errorTypeContentInArray(): string {
    return 'contentInArray';
  }
}
