import { Injectable } from '@angular/core';
import { SendInformationService } from '../SendInformation/send-information.service';
import { Observable } from 'rxjs';

declare const Android: any;

@Injectable({
  providedIn: 'root'
})
export class PostMessageService {

  constructor(private obser: SendInformationService<any>) {
    this.addListener();
  }

  /*
    Listener para capturar las respuestas de los
    post messages que vienen del apk.
  */
  addListener() {
    window.addEventListener('message', event => {
      const POST_MESSAGE_NAME = event.data.fnCallback;
      const POST_MESSAGE_RESPONSE = event.data.response;
      this.obser.sendData(POST_MESSAGE_RESPONSE, POST_MESSAGE_NAME);
    });
  }

  /*
    Función para llamar postMessages, retorna un obersevable para mirar las respuestas.
    @param {string} name: 'Nombre del postMessage que se quiere llamar.'
    @param {any} data: 'Objeto de datos para enviar al anfitrion por default es nulo.'
    @param {string} os: 'Sistema Operativo del anfitrion por default es Default.'
  */
  postMessage(name: string, data: any = null, os: 'iOS' | 'Android' | 'Default' = 'Default'): Observable<any> {
    const postMessageType = {
      iOS: () => (window as any).webkit.messageHandlers[name].postMessage(data),
      Android: () => Android[name](JSON.stringify(data)),
      Default: () => window.parent.postMessage({ event: name, data }, '*')
    };
    postMessageType[os]();
    return this.obser.getData(name);
  }
}
