import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { Observable, throwError, concat, of } from 'rxjs';
import { catchError, retryWhen, map, take, concatMap, delay, switchMap, tap } from 'rxjs/operators';
import { Router } from '@angular/router';
import { AuthService } from '../authentication/auth.service';
import { STATE_ERROR } from 'src/mbaas/mbaas.const';
import { environment as Environment } from '../../../../environments/environment';


@Injectable({
  providedIn: 'root'
})
export class InterceptorHttpService implements HttpInterceptor {

  reAutenticate: boolean;
  environment: any;

  constructor(
    private auth: AuthService,
    private router: Router,
  ) {
    this.reAutenticate = false;
    this.environment = Environment;
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    /* TODO:
    * PARA TRABAJAR LOCALMENTE ES NECESARIO AGREGAR UN FALSE, YA QUE EL EMULADOR
    * NO REENVIA EN EL INTERCEPTOR, AL MOMENTO DE MIGRARA A PRODUCCIONES ES
    * NECESARIO COMENTAR LA LINEA 25 Y DESCOMNEAR LA LIENA 26
    * */
    // if ( false &&
    if ( this.environment.interceptor &&
      this.auth.getAccessToken() &&
      req.headers.has('Authorization') &&
      this.auth.requiredRefreshToken(this.auth.getAccessToken()) &&
      this.reAutenticate === false
    ) {
      this.reAutenticate = true;
      return next.handle(req).pipe(
        take(1),
        concatMap(this.responseConcatMap(req, next))
      );
    }
    return next.handle(req);
  }

  responseConcatMap(req: HttpRequest<any>, next: HttpHandler): (result: any) => Observable<HttpEvent<any>> {
   return (result: any) => {
      return this.auth.reauthenticate().pipe(switchMap(this.responseSwitchMap(req, next)));
    };
  }

  responseSwitchMap(req: HttpRequest<any>, next: HttpHandler): (result: any) => Observable<HttpEvent<any>> {
    return (response: any): any => {
      if (response.state === STATE_ERROR) {
        this.router.navigate(['/forbidden']);
        return next.handle(req).pipe(take(1));
      }
      this.auth.setAccessToken(response.access_token, true);
      this.auth.setRefreshToken(response.refresh_token, true);
      return next.handle(req.clone({
        setHeaders: {
          Authorization: `Bearer ${ this.auth.getAccessToken() }`
        }
      }));
    };
  }

  // intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
  //   return next.handle(req).pipe(
  //     catchError(err => {
  //       return throwError(err);
  //     }),
  //     retryWhen(result =>
  //       result.pipe(take(1).
  //         // tslint:disable-next-line: no-shadowed-variable
  //         concatMap(result => {
  //           if (result.status === 401) {
  //             return this.auth.reauthenticate().pipe(switchMap( (response: any): any => {
  //               if (response.state === STATE_ERROR) {
  //                 this.router.navigate(['/forbidden']);
  //               } else {
  //                 this.auth.setAccessToken(response.access_token, true);
  //                 this.auth.setRefreshToken(response.refresh_token, true);
  //               }
  //               return next.handle(req.clone({
  //                 setHeaders: {
  //                   Authorization: `Bearer ${ this.auth.getAccessToken() }`
  //                 }
  //               }));
  //             }));
  //           }
  //           return throwError(result);
  //         })
  //       )
  //     )
  //   );
  // }
}
