import { Component } from '@angular/core';
import { Modal } from '../modal';

@Component({
  selector: 'mbaas-alert-modal',
  templateUrl: './alert-modal-tma.component.html',
  styleUrls: [
    './alert-modal-tma.component.scss',
  ]
})
export class AlertModalTMAComponent {

  data: Modal = {
    buttons: [
      {
        callback: null,
        buttonText: ''
      }
    ],
    title: '',
    message: '',
    display: false,
    entryComponent: null
  };
  show: boolean;

  constructor() {
    setTimeout( () => {
      this.show = true;
    }, 120);
  }

  destroy: any = () => {};

  onClose(valueTex) {
    return () => {
      this.data.display = false;
      setTimeout( () => {
        this.destroy();
      }, 250);
      return valueTex;
    };
  }

}
