import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'typeCard'
})
export class TypeCardPipe implements PipeTransform {

  transform(value: string): string {
    switch (value) {
      case 'CM':
        return 'Crédito Mastercard';
      case 'CV':
        return 'Crédito Visa';
      case 'AE':
        return 'American Express';
    }
  }
}
