import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CriptoService } from '../encryption/cripto.service';
import { AuthService } from '../authentication/auth.service';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';
import { CatalogoService } from '../catalogo/catalogo.service';
import { ONCALL, LOGGER, STATE_SUCESS, PAYLOAD, MODAL, STEP_ID, CLIENT_ID } from 'src/mbaas/mbaas.const';
import { MBAAS_STEPS, MBAAS_ROUTING } from 'src/mbaas/mbaas.route.const';
import { SendInformationService } from '../SendInformation/send-information.service';
import { ModalBuilderService } from '../ModalBuilder/modal-builder.service';
import { Modal } from '../../components/modal/modal';
import { Logger } from 'src/mbaas/logger/logger/logger';
import { AlertModalTMAComponent } from '../../components/modal/alert-modal-tma/alert-modal-tma.component';

@Injectable({
  providedIn: 'root'
})
export class WorkflowService {
  pro: boolean;
  payload: any;
  ipAddress: { ip?: string; } = {};
  constructor(
    private setPayload: SendInformationService<{ [key: string]: any }>,
    private setCurrentStep: SendInformationService<string>,
    private modalService: SendInformationService<Modal>,
    private obser: SendInformationService<boolean>,
    private logger: SendInformationService<Logger>,
    private router: Router,
    private http: HttpClient,
    private cripto: CriptoService,
    private auth: AuthService,
    private catalogo: CatalogoService,
    private modalBuilder: ModalBuilderService,
  ) {
    this.getIP();
    this.pro = environment.production;
  }

  getIP(): void {
    this.http.get<{ ip: string }>(environment.ip).subscribe(
      response => {
        this.ipAddress = response;
      }
    );
  }

  workflow(stepId: string, data: any): void {
    this.payload = data;
    if (!this.ipAddress.ip) {
      this.getIP();
      return;
    }
    if (stepId === MBAAS_STEPS.APPBOOT) {
      this.payload.ipAddress = this.ipAddress.ip;
    }

    this.logger.sendData({ title: 'Envio a Workflow', data: this.payload }, LOGGER);
    console.log('Payload enviado a workflow', this.payload);
    this.cripto.createEncryptor(this.cripto.getPublickeyMbaas());
    this.cripto.encrypter(this.payload).then(
      response => this.continueCallWorkflow(stepId, response)
    )
    .catch(err => {
      this.logger.sendData({ title: 'ERROR CRIPTO', data: err }, LOGGER);
      this.errorGeneralWorflow()(err);
    });
  }

  private continueCallWorkflow(stepId: string, encPayload: any): void {
    const workflowInterface: any = {
      stepId,
      payload: encPayload,
      clientId: this.auth.getClientId(),
      status: 1
    };
    const httpOptions = this.auth.headerTokenInjector();
    const strObj = JSON.stringify(workflowInterface);
    this.http.post<any>(environment.workflow, strObj, { headers: httpOptions })
      .subscribe(this.successWorkflow(), this.errorGeneralWorflow());
  }

  successWorkflow(): (response) => void {
    return response => {
      this.obser.sendData(false, ONCALL);
      if ( this.pro ) {
        this.cripto.unencrypter(response.data.payload).then( this.successUncrypter(response) ).catch( this.catchUncrypter() );
        return;
      }
      this.successUncrypter(response)(response.data.payload);
    };
  }

  successUncrypter(response): (data) => void {
    return data => {
      this.logger.sendData({ title: 'Recibido de Workflow', data: response }, LOGGER);
      console.log('Datos recibidos de Workflow', data);
      if (response.data.status === STATE_SUCESS) {
        this.setPayload.sendData(data, PAYLOAD);
        this.setCurrentStep.sendData(response.data.stepId, STEP_ID);
        this.router.navigate([this.getRoute(response.data.stepId)]);
        return;
      }
      // tslint:disable-next-line: variable-name
      this.catalogo.data(data.message).subscribe( (_response: any) => {
        this.modalService.sendData( this.modalBuilder.getModalData(_response[0], response.data.status, data), MODAL);
      }, this.errorGeneralWorflow());
    };
  }

  catchUncrypter(): (err) => void {
    return err => {
      console.log('Error Cripto.unEncrypter', err);
    };
  }

  errorGeneralWorflow(): (err) => void {
    return err => {
      this.obser.sendData(false, ONCALL);
      this.modalService.sendData( this.modalBuilder.getModalData({
        display: true,
          buttons: [
            {
              callback: this.callbackModalError500(),
              buttonText: 'Finalizar',
              class: 'button button--primary'
            }
          ],
          title: '',
          message: 'En este momento no podemos atender su solicitud, por favor intente más tarde.',
          entryComponent: AlertModalTMAComponent
      }, 3), MODAL);
    };
  }

  callbackModalError500(): (close: () => void) => void {
    return (close: () => void) => {
      close();
    };
  }

  getRoute(stepID: string): string {
    const routes = {};
    routes[MBAAS_STEPS.TEMP001.STEP] = `${MBAAS_ROUTING.PROYECTO}/temporales/${MBAAS_STEPS.TEMP001.ROUTE}`;
    routes[MBAAS_STEPS.TEMP002.STEP] = `${MBAAS_ROUTING.PROYECTO}/temporales/${MBAAS_STEPS.TEMP002.ROUTE}`;
    routes[MBAAS_STEPS.BEN001.STEP] = `${MBAAS_ROUTING.PROYECTO}/beneficios/${MBAAS_STEPS.BEN001.ROUTE}`;
    routes[MBAAS_STEPS.EVA001.STEP] = `${MBAAS_ROUTING.PROYECTO}/evaluador/${MBAAS_STEPS.EVA001.ROUTE}`;
    routes[MBAAS_STEPS.EVA002.STEP] = `${MBAAS_ROUTING.PROYECTO}/evaluador/${MBAAS_STEPS.EVA002.ROUTE}`;
    routes[MBAAS_STEPS.CCC001.STEP] = `${MBAAS_ROUTING.PROYECTO}/compra-cartera/${MBAAS_STEPS.CCC001.ROUTE}`;
    routes[MBAAS_STEPS.CCC002.STEP] = `${MBAAS_ROUTING.PROYECTO}/compra-cartera/${MBAAS_STEPS.CCC002.ROUTE}`;
    routes[MBAAS_STEPS.CCC003.STEP] = `${MBAAS_ROUTING.PROYECTO}/compra-cartera/${MBAAS_STEPS.CCC003.ROUTE}`;
    return routes[stepID] || MBAAS_ROUTING.FORBBIDEN;
  }
}
