import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'searchInArray',
  pure: true
})
export class SearchPipe implements PipeTransform {

// tslint:disable-next-line: max-line-length
  transform(
    items: any[] = [],
    regx: any = '',
    done: (item: any) => any = (item) => '',
    limit: number = 10,
    filtro: (item) => boolean = (item) => true
  ): any {
    if (!items) {
        return [];
    }
    let num = 0;
    const temp = items.filter( item => filtro(item)).filter(item => {
      if ( num < limit && done(item).toLowerCase().indexOf(regx.toLowerCase()) !== -1 ) {
        num++;
        return true;
      }
      return false;
    });
    return (num === 1 && done(temp[0]).toLowerCase() === regx.toLowerCase()) ? [] : temp;
  }
}
